
:root {
  --mainRowHeight: 70vh;
}

.App {
  text-align: center;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  height: 15vh;
  max-height: 150px;
}

.logo {
  padding-bottom: 0px;
  z-index: 100px;
}

.downloads strong {
  font-size: 24px;
  margin-right: 20px;
}

.downloads {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

body {
  background-color: #9edbf9;
  min-height: 95vh;
  padding-left: 20vw;
  padding-right: 20vw;
  padding-top: 10px;
  color: white;
  min-width: 720px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mainRow {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.bottomRow {
  display: flex;
  flex-direction: row;
  align-items: start;
  height: 88vh; /* Replace max-height with height */
  overflow: auto;
}

.imageHolder {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  min-height: 88vh;
}

.processButton {
  margin: 20px;
  width: 50%;
  height: 50px;
  font-size: 25px;
  font-weight: bold; /* Makes the text bold */
  color: white; /* Sets the text color to white */
  background: repeating-linear-gradient(
    45deg,
    #ee4035 0%,
    #ee4035 20%,
    #f37736 20%,
    #f37736 40%,
    #fdf498 40%,
    #fdf498 60%,
    #7bc043 60%,
    #7bc043 80%,
    #0392cf 80%,
    #0392cf 100%
  );
  border: none; /* Removes default border */
  border-radius: 5px; /* Optional: Rounds corners */
  padding: 10px 20px; /* Adds padding for the button */
  cursor: pointer; /* Adds pointer cursor on hover */
  text-transform: uppercase; /* Optional: Transforms text to uppercase */
  background-size: 200%; /* Enlarges the gradient for smoother transitions */
  transition: background 1s ease; /* Adds smooth transitions for hover effects */
  box-shadow: inset 0px 0px 3px 3px rgba(0, 0, 0, 0.1);;
}

.processButton.on {
  background-position: 100% 0; /* Moves the gradient on hover */
}

.processButton:hover {
  background-position: 100% 0; /* Moves the gradient on hover */
}


ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

li {
  color: black;
}
